<template>
    <div class="columns">
        <div class="xwsh_nr">
            <div class="pad-30">
                <a-row type="flex" justify="center" v-if="video">
                    <a-col :span="16" style="padding: 10px">
                        <chplayer :url="video" :max="true"></chplayer>
                    </a-col>
                    <a-col :span="8" style="padding: 10px">
                        <comment :id="article.id"/>
                    </a-col>
                </a-row>
            </div>
        </div>

        <slot name="suffix"></slot>
    </div>
</template>

<script>
    import chplayer from '@/components/player/chplayer/Index'
    import comment from './comment'

    export default {
        components: {chplayer, comment},
        props: {
            article: {type: Object}
        },
        computed: {
            videos() {
                if (!this.article.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.article.more.hasOwnProperty('videos')) {
                    return [];
                }
                return Array.isArray(this.article.more.videos) ? this.article.more.videos : [];
            },
            video() {
                return this.article.video_format;
            },
        },
        data() {
            return {}
        },
        mounted() {
            // console.log(this.article);
        }
    }
</script>
