<template>
    <div>
        <a-form @submit="handleSubmit" :autoFormCreate="(form)=>{this.form = form}">
            <a-form-item
                    fieldDecoratorId="comment_content"
            >
                <a-textarea placeholder="请写下评论" :autosize="{ minRows: 3, maxRows: 6 }"/>
            </a-form-item>
            <a-form-item
            >
                <a-button type='primary' htmlType='submit' style="float: right">
                    发表评论
