<template>
    <a-carousel arrows dotsClass="slick-dots slick-thumb" style="padding-bottom: 50px">
        <a slot="customPaging" slot-scope="props">
            <img :src="getImgUrl(props.i)"/>
        </a>
        <div v-for="item in list_computed">
            <img :src="item.url"/>
        </div>
    </a-carousel>
</template>
<script>
    export default {
        props: {
            list: {
                required: true,
                type: Array,
            }
        },
        data() {
            return {
                baseUrl: this.$store.state.common.fileBaseUrl,
            }
        },
        computed: {
            list_computed() {
                let temp = this.list;
                for (let i = 0; i < temp.length; i++) {

                    // if(temp[i].url.indexOf('http')){
                    //     continue;
                    // }

                    temp[i].url = this.baseUrl + temp[i].url
                }
                return temp
            }
        },
        methods: {
            getImgUrl(i) {
                return this.list_computed[i].url;
            }
        },
        mounted(){
            // console.log(this.list);
        }
    }
</script>
