<template>
    <div class="columns">
        <div class="xwsh_nr">
            <div style="padding: 30px 5px 30px 5px">
                <h1>{{article.post_title}}</h1>
                <h2>
                    发布日期：{{article.published_at_format}}
                    <a-divider type="vertical"/>
                    <template v-if="article.post_source">
                        来源：{{article.post_source}}
                        <a-divider type="vertical"/>
                    </template>
                    点击量：{{article.post_hits}}
                </h2>
                <a-divider/>

                <Album v-if="photos.length" :list="photos"/>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <div v-html="article.post_content"></div>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center" v-if="files.length">
                    <a-col :span="22">
                        <downloadFiles v-if="files.length" :list="files"/>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center" v-if="video">
                    <a-col :span="22">
                        <center>
                            <chplayer :url="video"></chplayer>
                        </center>
                    </a-col>
                </a-row>
            </div>
        </div>

        <slot name="suffix"></slot>
    </div>
</template>

<script>
    import Album from '@/components/carousel/Album'
    import downloadFiles from '@/components/download/Files'
    import chplayer from '@/components/player/chplayer/Index'

    export default {
        components: {Album, downloadFiles, chplayer},
        props: {
            article: {type: Object}
        },
        computed: {
            photos() {
                if (!this.article.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.article.more.hasOwnProperty('photos')) {
                    return [];
                }
                return Array.isArray(this.article.more.photos) ? this.article.more.photos : [];
            },
            files() {
                if (!this.article.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.article.more.hasOwnProperty('files')) {
                    return [];
                }
                return Array.isArray(this.article.more.files) ? this.article.more.files : [];
            },
            videos() {
                if (!this.article.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.article.more.hasOwnProperty('videos')) {
                    return [];
                }
                return Array.isArray(this.article.more.videos) ? this.article.more.videos : [];
            },
            video() {
                return this.article.video_format;
            },
        },
        data() {
            return {}
        },
        mounted() {
            // console.log(this.article);
        }
    }
</script>
