import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'article_comments/';

export const getArticleCommentList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        params: data,
        method: 'get'
    })
}

export const getArticleComment = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const createArticleComment = (d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl,
        method: 'post',
        data,
        params,
    })
}

export const updateArticleComment = (id, d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'put',
        data,
        params,
    })
}

export const deleteArticleComment = (id) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'delete',
        params,
    })
}
