<template>
    <div id="video" :style="getStyle">
        加载中
    </div>
</template>

<script>
    import {getImageUrl} from "@/libs/tools";

    export default {
        name: "Index",
        props: {
            url: {
                required: true,
                type: String,
            },
            thumbnail: {
                required: false,
                type: String,
                default() {
                    return '';
                }
            },
            max: {
                required: false,
                type: Boolean,
                default() {
                    return false;
                }
            },
        },
        computed: {
            getStyle() {
                return this.max ? {} : {width: '600px', height: '400px'};

            }
        },
        mounted() {
            if (this.url === '') {
                this.$message.error('获取视频地址失败');
                return false;
            }

            let videoObject = {
                logo: '党建视频', //设置logo，非必须
                container: '#video',//“#”代表容器的ID，“.”或“”代表容器的class
                variable: 'player',//该属性必需设置，值等于下面的new chplayer()的对象
                video: getImageUrl(this.url),//视频地址
                autoplay: false,//是否自动播放，默认true=自动播放，false=默认暂停状态
            };

            if (this.thumbnail !== '') {
                videoObject.poster = this.thumbnail;
            }
            var player = new chplayer(videoObject);
        }
    }
</script>

