<template>
    <a-card>
        <components :article="data" :is="articleTemplate">
            <slot name="suffix" slot="suffix"></slot>
        </components>
    </a-card>
</template>

<script>
    import article_component from './components/Article'
    import article_video_comment_component from './components/ArticleVideoComment'
    import {getArticle, checkArticle} from '@/api/article'

    export default {
        name: "Index",
        components: {article_component, article_video_comment_component},
        props: {
            id: {required: true, type: [Number, String]},
            checkAnyway: {
                required: false, type: Boolean, default() {
                    return false;
                }
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            },
        },
        data() {
            return {
                data: {},
                articleTemplate: '',
            }
        },
        methods: {
            init() {
                let request = this.checkAnyway ? getArticle(this.thisId) : checkArticle(this.thisId);
                request
                    .then(res => {
                        // if (!res.data.template) {
                        //     this.$message.error(`模板读取错误`);
                        //     return;
                        // }

                        let tpl = res.data.template ? res.data.template : 'article';
                        this.articleTemplate = tpl + '_component';
                        this.data = res.data;
                    })
            }
        },
        watch: {
            $route: {
                handler() {
                    this.init();
                },
                immediate: true
            }
        }
    }
</script>

